"use client";

import React, { useState } from "react";
import { Navbar } from "@/components/Navbar";
import { Button } from "@/components/ui/button";
import { Plus, Minus, RotateCcw, Loader2 } from "lucide-react";
import { IncreaseButton } from "@/components/IncreaseButton";
import { DecreaseButton } from "@/components/DecreaseButton";
import { motion, AnimatePresence } from "framer-motion";
import { useAccount, useContract, useReadContract, useSendTransaction } from "@starknet-react/core";
import { COUNTER_ABI } from "@/abi/counter_abi";
import { COUNTER_CONTRACT_ADDRESS } from "@/lib/utils";

export default function Home() {
  const [inputValue, setInputValue] = useState("1");
  const numericValue = Math.max(0, Math.floor(Number(inputValue) || 0));

  const { address, isConnected } = useAccount();
  const { contract } = useContract({
    abi: COUNTER_ABI,
    address: COUNTER_CONTRACT_ADDRESS as `0x${string}`,
  });

  const { data: count, isPending: countLoading, isFetching: countFetching } = useReadContract({
    abi: COUNTER_ABI,
    address: COUNTER_CONTRACT_ADDRESS as `0x${string}`,
    functionName: "get_count",
    args: [],
    watch: true,
    enabled: true,
  });

  const { send: sendTx, isPending: txPending } = useSendTransaction({});

  const handleIncreaseBy = () => {
    if (!contract || !address || numericValue <= 0) return;
    const call = contract.populate("increase_count_by", [BigInt(numericValue)]);
    sendTx([call]);
  };

  const handleDecreaseBy = () => {
    if (!contract || !address || numericValue <= 0) return;
    const call = contract.populate("decrease_count_by", [BigInt(numericValue)]);
    sendTx([call]);
  };

  const displayCount = count !== undefined && count !== null ? Number(count) : 0;
  const countLoadingState = countLoading || countFetching;
  const canTransact = isConnected && !!address && !txPending && numericValue > 0;

  return (
    <main className="min-h-screen flex flex-col bg-background selection:bg-primary/20">
      <Navbar />

      <div className="flex-1 flex flex-col items-center justify-center p-4 relative overflow-hidden">
        <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[500px] h-[500px] bg-primary/20 rounded-full blur-[120px] pointer-events-none opacity-50" />

        <div className="relative z-10 flex flex-col items-center gap-12 max-w-md w-full">
          <div className="text-center space-y-2">
            <h1 className="text-4xl sm:text-5xl font-extrabold tracking-tight lg:text-6xl bg-gradient-to-br from-foreground to-muted-foreground bg-clip-text text-transparent">
              Counter
            </h1>
            <p className="text-muted-foreground text-lg">
              Manage your count on Starknet.
            </p>
          </div>

          <div className="w-full aspect-square max-w-[320px] bg-card/50 backdrop-blur-xl border border-border/50 rounded-3xl shadow-2xl flex items-center justify-center relative group">
            <div className="absolute inset-x-0 top-0 h-px bg-gradient-to-r from-transparent via-foreground/10 to-transparent opacity-50" />
            <div className="absolute inset-x-0 bottom-0 h-px bg-gradient-to-r from-transparent via-foreground/10 to-transparent opacity-50" />

            {countLoadingState && displayCount === 0 ? (
              <Loader2 className="h-16 w-16 animate-spin text-muted-foreground" />
            ) : (
              <AnimatePresence mode="popLayout">
                <motion.span
                  key={displayCount}
                  initial={{ opacity: 0, y: 20, scale: 0.8 }}
                  animate={{ opacity: 1, y: 0, scale: 1 }}
                  exit={{ opacity: 0, y: -20, scale: 0.8 }}
                  transition={{ type: "spring", stiffness: 300, damping: 20 }}
                  className="text-9xl font-bold tracking-tighter tabular-nums text-foreground drop-shadow-xl"
                >
                  {displayCount}
                </motion.span>
              </AnimatePresence>
            )}
          </div>

          <div className="w-full space-y-4">
            <div className="flex flex-col sm:flex-row items-center gap-3">
              <label htmlFor="count-value" className="text-sm font-medium text-muted-foreground whitespace-nowrap">
                Value:
              </label>
              <input
                id="count-value"
                type="number"
                min={0}
                value={inputValue}
                onChange={(e) => setInputValue(e.target.value)}
                className="w-full sm:w-28 px-3 py-2 rounded-lg border border-input bg-background text-foreground font-mono text-center focus:outline-none focus:ring-2 focus:ring-ring"
              />
            </div>
            <div className="flex flex-wrap items-center justify-center gap-3">
              <Button
                onClick={handleDecreaseBy}
                disabled={!canTransact}
                variant="outline"
                className="rounded-2xl border-2 gap-2 min-w-[180px]"
              >
                {txPending ? (
                  <Loader2 className="h-5 w-5 animate-spin" />
                ) : (
                  <Minus className="h-5 w-5" />
                )}
                Decrease count by {numericValue}
              </Button>
              <Button
                onClick={handleIncreaseBy}
                disabled={!canTransact}
                variant="outline"
                className="rounded-2xl border-2 gap-2 min-w-[180px]"
              >
                {txPending ? (
                  <Loader2 className="h-5 w-5 animate-spin" />
                ) : (
                  <Plus className="h-5 w-5" />
                )}
                Increase count by {numericValue}
              </Button>
            </div>
          </div>

          {!isConnected && (
            <p className="text-sm text-muted-foreground text-center">
              Connect your wallet to change the counter.
            </p>
          )}
        </div>
      </div>
    </main>
  );
}
